import React from 'react'

import { View } from 'react-native'

import { render } from '@testing-library/react-native'

import { NeomorphBlurView } from './NeomorphBlurView'

describe('NeomorphBlurView Component', () => {
  it('renders correctly', () => {
    const { getByTestId } = render(
      <NeomorphBlurView>
        <View testID="child-view" />
      </NeomorphBlurView>,
    )

    const neomorphBlurView = getByTestId('neomorph-blur-view')
    const childView = getByTestId('child-view')

    // Check if the NeomorphBlurView renders
    expect(neomorphBlurView).toBeTruthy()

    // Check if the child view renders
    expect(childView).toBeTruthy()
  })
})
